import React, { Component } from 'react';
import axios from 'axios';


export default class Home extends Component {
    constructor() {
        super();
        this.state = {
            dogs: []
        }
    }


    componentDidMount() {
        axios.get('https://dog.ceo/api/breeds/image').then(res => {
            this.setState({
                dogs: res.data
            })
        })
    }
    render() {
        return (
            <div>Welcome Bruh!
                {this.state.dogs}
            </div>
        )
    }
}



